import React, { Component } from 'react';
import {
    TransitionablePortal,
    Segment,
    Button,
    Label,
    Icon
} from 'semantic-ui-react';

import KeypadButton from './KeypadButton';


function KeypadRow(props) {
    return (
        <Button.Group widths="3" compact className="black" style={{height:40}}>
            {props.children}
        </Button.Group>
    )
}

class IndexPortal extends Component {

    state = {
        selectedLetter: "",
        searchMode:this.props.searchMode,
        startingLetters: []
    }

    preventTouchMove = (e) => {
        // console.log('preventTouchMove',e, e.type);
        if (e.type === "touchmove") {
            // console.log("preventing touchmove");
            e.preventDefault();
        }
        e.stopPropagation();
    }

    componentWillReceiveProps = (nextProps) => {
        if (nextProps.items.length === this.props.items.length)
            return;
        let startLetters=[];
        nextProps.items.forEach((item) => {
          startLetters.push(item.name.charAt(0).toUpperCase());
        });
        let distinctLetters = Array.from(new Set(startLetters));
        this.setState({startingLetters:distinctLetters});
    }

    shouldComponentUpdate = (nextProps, nextState) => {
        // console.log(nextProps.open !== this.props.open);
        return (nextProps.open !== this.props.open || this.state.startingLetters.length === 0 );
    }

    // onOpenAZ = (e) => {
    //     let startLetters=[];
    //     this.props.items.forEach((item) => {
    //       startLetters.push(item.name.charAt(0).toUpperCase());
    //     });
    //     let distinctLetters = Array.from(new Set(startLetters));
    //     this.setState({startingLetters:distinctLetters});
    // }    
        
    render() {  
        const renderKey = (label) => {
            return (<KeypadButton label={label} {...this.props} disabled={!this.state.startingLetters.includes(label) && !this.props.searchMode}/>)
        }
        return (
            <div>
                <TransitionablePortal
                    open={this.props.open && this.props.items.length > 1}
                    transition={{animation:'horizontal flip', duration:500}}
                    closeOnDocumentClick={false}
                    // onClose={this.props.onClose}
                    // onOpen={this.onOpenAZ}
                >  
                    <Segment basic inverted floated="right" textAlign="center" 
                        style={{
                            marginRight:0,
                            paddingTop:0,
                            borderRadius:"0em",
                            position:'fixed',
                            top: '62px',
                            right: '0%',
                            zIndex: 5002,
                            height:"calc(100% - 53px)",
                            width:"50%"
                        }}  
                        onClick={this.preventTouchMove}
                        onTouchMove={this.preventTouchMove}
                        onTouchStart={this.preventTouchMove}
                    >
                        <KeypadRow>
                            {renderKey("A")}
                            {renderKey("B")}
                            {renderKey("C")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("D")}
                            {renderKey("E")}
                            {renderKey("F")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("G")}
                            {renderKey("H")}
                            {renderKey("I")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("J")}
                            {renderKey("K")}
                            {renderKey("L")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("M")}
                            {renderKey("N")}
                            {renderKey("O")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("P")}
                            {renderKey("Q")}
                            {renderKey("R")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("S")}
                            {renderKey("T")}
                            {renderKey("U")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("V")}
                            {renderKey("W")}
                            {renderKey("X")}
                        </KeypadRow>
                        <KeypadRow>
                            {renderKey("Y")}
                            {renderKey("Z")}
                            <Button
                                style={{borderRadius:"0em"}}
                                disabled={!this.props.searchMode}
                                content="⌫"
                                onClick={this.props.letterSelected.bind(this, "⌫")} 
                            />
                        </KeypadRow>
                        <Button className="black" onClick={this.props.letterSelected.bind(this, " ")} disabled={!this.props.searchMode} style={{margin:10,borderRadius:0}}>[SPACE]</Button>
                        <Label className="black" attached="bottom" >
                            <Button  secondary icon={<Icon name="reply" flipped="vertically" size="big"/>}  onClick={this.props.doneClicked} style={{borderRadius:0, padding:20, paddingBottom:15, pacity:0.7}}/>
                        </Label>
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default IndexPortal;



